

.treemain {
    display: flex;
    width: 100%;
    justify-content: center;
}

.treearea {
    margin-left: 10px;
    display: block;
    width: 500px;
    overflow: scroll ;
    height: 1000px;
    resize: horizontal;
    overflow: auto;
    white-space: nowrap;
}
.edgearea {
    cursor: col-resize;
    width: 50px;
    height: 1000px;
    background-color: red;
}

.nodedetail {
    display: block;
    background-color: #e9e4d9;
    width: 70%;
    overflow: scroll;height: 1000px;
    
}


.treeele {
    font-size:30px;
    animation-duration: 0.5s;
	animation-name: slidein;
}


.searcondition_area{
    display: flex;
    justify-content: start;
    align-items: start;
}