html, body {
	width: 100%;
	overflow-x: hidden;
}
body {
	justify-content:center;
	position: fixed; 
	left: 0; 
	top: 0; 

	height:100%;
	font-family: "Lato", "Serif";
	font-weight: 300;
	padding:0;
	margin:0;
	font-size:16px;
	line-height: 28px;
	color: #777; 
	background: #fff;
	overflow-x:hidden;
}
h1 { font-size: 3em; line-height:1.2em; margin:0 0 .3em; }
h2 { font-size: 1.8em; line-height:1.4em; margin:0 0 .3em;  }
h3 { font-size: 1.4em; line-height:1.4em; }
h4 { font-size: 1.25em; line-height:1.4em;}
h5 { font-size: 1.1em; line-height:1.4em; }
h6 { font-size: 1em; line-height:1.2em; }

h1 a, h2 a, h3 a, h4 a, h5 a, h6 a {
	text-decoration: none
}	 
p { 
	padding: 0 0 1em;
	margin:0;
}
a {
	color: #8442b7;
	outline: 0;
	font-weight: bold;
	-webkit-transition: all .8s ease;
	transition: all .8s ease;
} 
a:hover {
	text-decoration: none;
	color: #1f2222;
	-webkit-transition: all .8s ease;
	transition: all .8s ease;
}
.highlight {
	color: #8442b7;
}

.msg {
	color: white;
	text-align: left;
	border-radius: 10px;
	border-width: 1px;
	animation-duration: 0.5s;
	animation-name: slidein;
	font-weight:bold;
	font-size:20px;
	margin-top:4px;
	margin-bottom:4px;
}
  
@keyframes slidein {
	from {
		margin-left: -60%;
		width: 20%
	}

	to {
		margin-left: 0%;
		width: 100%;
	}
}

.searchcondtion-area {
