	border-color: #8442b7;
	border-radius: 5px;
	
}

.popup {
	display:flex;
	justify-content:center;
	position: fixed; 
	left: 0; 
	top: 0;  
	width: 100%; 
	height: 100%; 
	padding-top: 5%;
	background-color: rgba(100, 100, 100, 0.1);
	overflow: scroll;
	height: 90%;
