}

.loader {
	position: fixed; 
	left: 0; 
	top: 0; 
	z-index: 999; 
	width: 100%; 
	height: 100%; 
	overflow: visible;
	background-color: rgba(110,100,100,0.2);
	
  }
.loader .ball {
	background: orange;
	border-radius: 5px;
	display: block;
	height: 10px;
	left: -10px;
	position: absolute;
	top: 5px;
	width: 10px;
	animation-duration: 2s;
	animation-iteration-count: infinite;
	animation-name: alongfromleft;
	animation-timing-function: ease-in-out;
	margin-top:24%;
  }
.loader .ball2 {
	background: lightskyblue;
	border-radius: 5px;
	display: block;
	height: 10px;
	left: -10px;
	position: absolute;
	top: 5px;
	width: 10px;
	animation-duration: 2s;
	animation-iteration-count: infinite;
	animation-name: alongfromight;
	animation-timing-function: ease-in-out;
	margin-top:26.4%;
  }
  .loader ul {
	margin: 0;
	padding: 0;
	margin-top:25%;
  }
  .loader li {
	background-color: orange;
	border-radius: 5px;
	display: inline-block;
	margin: 0;
	height: 10px;
	width: 30px;
	animation-duration: 1s;
	animation-iteration-count: infinite;
	animation-name: around;
	animation-timing-function: linear;
  }
  .loader li:nth-child(1) {
	animation-delay: 0s;
  }
  .loader li:nth-child(2) {
	background-color: lightgreen;
	animation-delay: 0.2s;
  }
  .loader li:nth-child(3) {
	background-color: #8442b7;
	animation-delay: 0.4s;
  }
  .loader li:nth-child(4) {
	background-color: lightpink;
	animation-delay: 0.6s;
  }
  .loader li:nth-child(5) {
	background-color: lightskyblue;
	animation-delay: 0.8s;
  }
  
  @keyframes around {
	0% {
	  -webkit-transform: rotate(0deg);
	}
	100% {
	  -webkit-transform: rotate(359deg);
	}
  }

  @keyframes alongfromleft {
	0% {
		left: 45%;
		top: 5px;
	}
	25% {
	  	left: 54%;
		top: 5px;
	}
	50% {
		left: 54%;
		top: 45px
	}
	75% {
		left: 45%;
		top: 45px
	}
	100% {
		left: 45%;
	  	top: 5px;
	}

  }

  @keyframes alongfromight {
	0% {
		left: 54%;
		top: 0px;
	}
	25% {
		left: 45%;
		top: 0px;
	}
	50% {
		left: 45%;
		top: -40px;
	}
	75% {
		left: 54%;
		top: -40px;
	}
	100% {
		left: 54%;
		top: 0px;
	}
  }


