	animation-delay: 0.45s;
} 

.loading-element span:nth-child(10) {
	left: 95%;
	animation-delay: 0.50s;
} 

@keyframes loadingaction {
	0%{
		height: 0px;
		bottom:0px
	}
	50%{
		height: 100%;
		bottom:0px
	}
	100%{
		height: 0px;
		bottom:0px
	}
	
}

