.loadingwrapper3 div {
	position: absolute;
	border-radius: 100%;
	animation-iteration-count: infinite;
	animation-timing-function: linear;
	animation-duration: .5s;
	border-width: 6px;
}
.loadingwrapper3 div:nth-child(odd){
	border-color: #8442b7;
}
.loadingwrapper3 div:nth-child(even){
	border-color:white;
}

.loadingwrapper3 div:nth-child(1) {
	display: block;
	width: 100%;
	height: 100%;
	border-top-style: solid;
	animation-name: docircle-1;
}
.loadingwrapper3 div:nth-child(2) {
	display: block;
	width: 80%;
	height: 80%;
	border-right-style: solid;
	animation-name: docircle-2;
}
.loadingwrapper3 div:nth-child(3) {
	display: block;
	width: 60%;
	height: 60%;
	border-bottom-style: solid;
	animation-name: docircle-1;
}
.loadingwrapper3 div:nth-child(4) {
	display: block;
	width: 40%;
	height: 40%;
	border-left-style: solid;
	animation-name: docircle-2;
}

@keyframes docircle-1 {
	0%{
		transform: rotate(0deg);
	}
	100%{
		transform: rotate(360deg);
	}
}
@keyframes docircle-2 {
	0%{
		transform: rotate(0deg);
	}
	100%{
		transform: rotate(-360deg);
	}
}

