.logo {
	content: "";
	width: 20%;
	left: 6%;
	height: 75px;
	position: fixed;
	top: 96px;
	margin-top: -86px;
	z-index: 10;
	overflow: hidden;
}
 
.logo::before, .logo::after{
	position: absolute;
	width: 100%;
	height: 100%;
	font-weight: bold;
	justify-content: center;
	align-content: center;
	margin-top: 4%;
}
.logo::before {
	
	font-size: 34px;
	content: "Channel A System";
	left: 18px;
	top : 0;
	color: #8442b7;
	animation: slideinfromTop;
	animation-duration: 0.5s;
	animation-delay: 0.3s;
	animation-timing-function: linear;  
}

@keyframes slideinfromTop {
	from {
		top: -100%;
	}
	to {
		top: 0;
	}
}


.logo::after{
	font-size: 11.4px;
	text-transform: uppercase;
	content: "POC for Samsung Electronics Customer MDM";
	color: orange;
	text-align: left;
	left: 18%;
	width: 130%;	 
	top: 39%;
	animation-name: slideinfromBottom;
	animation-duration: 0.5s;
	animation-delay: 0.3s;
	animation-timing-function: linear;  
} 


@keyframes slideinfromBottom {
	from {
		top: 100%;
	}
	to {
		top: 40%;
	}
}

